@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ecedf2;
  margin: 2rem;
}
.card {
  height: auto;
  width: 330px;
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
  font-family: "Poppins", sans-serif;
}
.top_part {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.circle {
  display: flex;
}
.circle span {
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background-color: #ccecfe;
  margin-right: 8px;
  cursor: pointer;
  transition: all 0.5s;
  border: 1px solid #ccecfe;
}
.circle span:hover {
  border: 2px solid #c7e9ff;
  background-color: #51bdfe;
}
.top_part small {
  height: 25px;
  width: 25px;
  background-color: #efefef;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.top_part small i {
  margin-top: 2px;
  cursor: pointer;
  transition: all 0.5s;
}
.fa-heart {
  color: red;
  margin-top: 2px;
}
.image {
  display: flex;
  justify-content: center;
  align-items: center;
}
.image img {
  width: 150px;
  margin-bottom: 30px;
}
.vitamin {
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.rating {
  display: flex;
  flex-direction: row-reverse;
  float: left;
}
.rating > input {
  display: none;
}
.rating > label {
  position: relative;
  width: 15px;
  font-size: 19px;
  color: #ff0000;
  cursor: pointer;
}
.rating > label::before {
  content: "\2605";
  position: absolute;
  opacity: 0;
}
.rating > label:hover:before,
.rating > label:hover ~ label:before {
  opacity: 1 !important;
}
.rating > input:checked ~ label:before {
  opacity: 1;
}
.rating:hover > input:checked ~ label:before {
  opacity: 0.4;
}
.reviews {
  margin-top: 5px;
}
.reviews p {
  font-size: 12px;
  font-weight: 700;
  color: #9f9f9f;
}
.reviews u {
  font-size: 11px;
  color: red;
  cursor: pointer;
  transition: all 0.5s;
}
.size p {
  font-size: 12px;
  font-weight: 700;
  color: #9f9f9f;
}
.size h5 {
  margin-top: 10px;
}
.buttons {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}
.buttons button {
  width: 100%;
  height: 40px;
  border: none;
  border-radius: 30px;
  cursor: pointer;
}
.buttons button p {
  font-size: 10px;
}
.buttons button:nth-child(1) {
  background-color: #fe6424;
  color: #fff;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.5s;
}
.buttons button:nth-child(1):hover {
  background-color: #c73f07;
}
.buttons button:nth-child(2) {
  background-color: #eff0f2;
  color: #89898a;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.5s;
}
.buttons button:nth-child(2):hover {
  background-color: #b7bbc3;
  color: #606060;
}
.card h4 {
  margin-top: 10px;
}
.gender {
  margin-top: 10px;
  display: flex;
  gap: 10px;
  width: 70%;
}
.gender span {
  height: 25px;
  width: 30%;
  background-color: #eff0f2;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.5s;
  font-weight: 700;
  color: #8e8e8e;
}
.gender span:hover {
  background-color: #b4b6b9;
  color: #605c5c;
}
.last_buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
.qty_btn {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: 100px;
  border-radius: 25px;
  background-color: #eff0f2;
  font-size: 15px;
  cursor: pointer;
  transition: all 0.5s;
}
.qty_btn:hover {
  background-color: #9d9fa3;
}
.qty_btn p {
  padding: 0 15px;
  font-weight: 700;
}
.money_bag {
  display: flex;
  justify-content: center;
  align-items: center;
}
.money_bag h3 {
  margin-right: 5px;
}
.money_bag button {
  height: 40px;
  width: 100px;
  background-color: #51beff;
  border: none;
  border-radius: 25px;
  font-size: 12px;
  color: #fff;
  cursor: pointer;
  transition: all 0.5s;
}
.money_bag button:hover {
  background-color: #107bbb;
}
.money_bag button i {
  margin-right: 5px;
}

.price {
  padding-top: 10px;
}

.description {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  width: 100%;
}
